const GET_DATA = 'GET_DATA';

const init = {objects: [1,2,3]};

const reducer = (state = init, action) => {
	if (action === undefined) return state;
	switch (action.type) {
		case GET_DATA: {
			return {...state, objects: [...state.objects, action.payload]};
		}

		default: {
			return state
		}
	}
};

const actions = {
	addObject: newObject => {
		return {
			type: GET_DATA,
			payload: newObject
		}
	}
};

export {
	reducer as objects,
	actions as objectActions
}



