import React, {Component} from "react"
import {Link} from "react-router-dom"
import Store from "./Store";
import * as event from "delegates";

class AddNewProduct extends Component {
	constructor() {
		super();
		this.state = {
			product: {
				name: "",
				category: "",
				price: "",
				description: ""
			}
		}
	}

	createNewProduct(name, category, price, description) {
		this.setState({
			product: {
				name: name,
				category: category,
				price: price,
				description: description
			}
		})
	}

	handleProdName(event) {
		this.setState({
			product: {
				name: event.target.value
			}
		})
	}

	handleCategory(event) {
		this.setState({
			product: {
				category: event.target.value
			}
		})
	}

	handlePrice(event) {
		this.setState({
			product: {
				price: event.target.value
			}
		})
	}

	handleDescription(event) {
		this.setState({
			product: {
				description: event.target.value
			}
		})
	}

	submit(){
	//	code for POST method this.state.product
	}


	render() {
		return (
			<Store>
				<div>
					<p>Add new product to the DB:</p>
					<br/>
					<br/>
					<input type="text" value={this.state.product.name} onChange={(event) => this.handleProdName(event)}/><br/>
					<input type="text" value={this.state.product.category} onChange={(event) => this.handleCategory(event)}/><br/>
					<input type="text" value={this.state.product.price} onChange={(event) => this.handlePrice(event)}/><br/>
					<input type="text" value={this.state.product.description} onChange={(event) => this.handleDescription(event)}/><br/>
					<br/>
					<button className="btn btn-primary" onClick={this.submit.bind(this)}>Submit new product object to the server</button>

					<p>{this.state.product.name}</p>
				</div>
			</Store>
		)
	}
}

export default AddNewProduct



